/// Import order:
/// 1.  Encoding
/// 2.  Variables
/// 3.  Foundation
/// 4.  Utilites
/// 5.  Font Awesome
/// 6.  Global Styles
/// 7.  Theme CMS Styles
/// 8.  Third Party
/// 9.  Atoms
/// 10. Wrappers
/// 11. Menus
/// 12. Components
/// 13. Content Types
/// 14. Pages
/// 15. Print

/// Encoding
/// --------
@charset 'UTF-8';

/// Variables
/// ---------
@import "variables/_animation.scss";
@import "variables/_colors.scss";
@import "variables/_foundation.scss";
@import "variables/_type.scss";

/// Foundation
/// ----------
/// Source: http://foundation.zurb.com/docs/
/// Notes:
/// 1. Foundation's variables are imported first. We only import the variables
///    needed for grid and flex classes.
/// 2. Modify the _foundation.scss file to alter grid settings.
/// 3. Only include XY Grid and Flex Classes.
@import 'foundation';
// Include grid classes.
@include foundation-xy-grid-classes(
  $base-grid: true,
  $margin-grid: true,
  $padding-grid: true,
  $block-grid: true,
  $collapse: true,
  $offset: true,
  $vertical-grid: true,
  $frame-grid: false
);
@include foundation-flex-classes;

/// Utilities
/// ---------
@import "utilities/_mixins.scss";

/// Font Awesome
